"use client";

import { useState , useRef ,useEffect} from "react";

export default function AskPage() {
  const [messages, setMessages] = useState([
    { sender: "bot", text: "Hi üëã! Ask me anything about your documents." },
  ]);
  const [question, setQuestion] = useState("");
  const [loading, setLoading] = useState(false);
  const chatEndRef = useRef(null);

    // Auto-scroll to bottom when messages update
    useEffect(() => {
      chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }, [messages, loading]);
  
  const handleAsk = async () => {
    if (!question.trim()) return;
    setLoading(true);

    // Add user message to chat
    const newMessages = [...messages, { sender: "user", text: question }];
    setMessages(newMessages);

    try {
      const res = await fetch("http://localhost:8000/ask", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ question, top_k: 3 }),
      });

      if (!res.ok) {
        throw new Error("Failed to fetch from backend");
      }

      const data = await res.json();

      // Add bot response
      setMessages([
        ...newMessages,
        { sender: "bot", text: data.answer || "Sorry, I couldn‚Äôt find an answer." },
      ]);
    } catch (err) {
      console.error(err);
      setMessages([
        ...newMessages,
        { sender: "bot", text: "‚ö†Ô∏è Error: Could not fetch answer." },
      ]);
    } finally {
      setLoading(false);
      setQuestion(""); // clear input
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-black text-white p-6">
      <h1 className="text-3xl font-bold text-purple-400 mb-6">RAG Chatbot</h1>

      <div className="w-full max-w-2xl flex flex-col bg-gray-900 rounded-2xl shadow-lg p-6">
        {/* Chat window */}
        <div className="flex-1 overflow-y-auto mb-4 space-y-4 max-h-[70vh]">
          {messages.map((msg, idx) => (
            <div
              key={idx}
              className={`flex ${
                msg.sender === "user" ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`px-4 py-2 rounded-2xl max-w-xs ${
                  msg.sender === "user"
                    ? "bg-purple-600 text-white"
                    : "bg-gray-700 text-purple-200"
                }`}
              >
                {msg.text}
              </div>
            </div>
          ))}
          {loading && (
            <div className="flex justify-start">
              <div className="px-4 py-2 rounded-2xl bg-gray-700 text-purple-200 animate-pulse">
                Thinking...
              </div>
            </div>
          )}
        </div>

        {/* Input box */}
        <div className="flex items-center space-x-2">
          <input
            type="text"
            className="flex-1 p-3 rounded-xl bg-gray-800 border border-gray-600 text-white focus:outline-none focus:ring-2 focus:ring-purple-500"
            placeholder="Type your question..."
            value={question}
            onChange={(e) => setQuestion(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && handleAsk()}
          />
          <button
            onClick={handleAsk}
            disabled={loading}
            className="bg-purple-600 hover:bg-purple-700 text-white px-4 py-2 rounded-xl disabled:bg-gray-600"
          >
            Send
          </button>
        </div>
      </div>
    </div>
  );
}
